<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">旧胎退回</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}旧胎退回</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="TableDataRef" :model="params" class="demo-form-inline">
        <el-form-item label="库存组织" size="mini" prop="company_id" :rules="[{ required: true, message: '请选择库存组织', trigger: 'change' }]">
          {{(function(){if(params.company_id == 0)params.company_id = null})()}}
          <el-select v-model="params.company_id" placeholder="请选择" :disabled="!!tirebackstorageAddGet.outstock_id" @change="companyChange" filterable @focus="companyListInvok">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="[{ required: true, message: '请选择业务日期', trigger: 'change' }]">
          <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="出库类型：" size="mini">
          <span>旧胎退回</span>
        </el-form-item>
        <el-form-item label="选择客户" size="mini">
          <el-input v-model="params.customer_name" disabled></el-input>
          <!-- wz alter -->
          <!-- <span style="font-size:12px">{{params.customer_name}}</span> -->
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          {{(function(){if(params.business_id == 0)params.business_id = null})()}}
          <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok" filterable>
            <el-option v-for="item in employeeList" :label="item.employee_name" :value="item.employee_id" :key="item.employee_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="收货人" size="mini" prop="consignee_name">
          <el-input v-model="params.consignee_name"></el-input>
        </el-form-item>
        <el-form-item label="收货人手机号" size="mini">
          <el-input v-model="params.consignee_mobile"></el-input>
        </el-form-item>
        <el-form-item label="所属区域" size="mini">
          <getAreaList v-if="defaultArea && defaultArea.length" @changeDefaultArea="changeDefaultArea" :defaultArea="defaultArea" />
        </el-form-item>
        <el-form-item size="mini" label="详细地址">
          <el-input v-model="params.address"></el-input>
        </el-form-item>
        <el-form-item label="送货需求" size="mini">
          {{(function(){if(params.delivery_demand_id == 0)params.delivery_demand_id = null})()}}
          <el-select clearable v-model="params.delivery_demand_id" @focus="getDeliveryDemandListInvok">
            <el-option v-for="item in deliveryDemandList" :label="item.delivery_demand_name" :value="item.delivery_demand_id" :key="item.delivery_demand_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交货方式" size="mini">
          {{(function(){if(params.hand_over_id == 0)params.hand_over_id = null})()}}
          <el-select clearable v-model="params.hand_over_id" @focus="getHandoverListInvok">
            <el-option v-for="item in handoverList" :label="item.hand_over_name" :value="item.hand_over_id" :key="item.hand_over_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="回款方式" size="mini" prop="payment_method">
          {{(function(){if(params.intentional_logistics_id == 0)params.intentional_logistics_id = null})()}}
          <el-select v-model="params.payment_method">
            <el-option label="客户自结" :value="1">客户自结</el-option>
            <el-option label="物流代收" :value="2">物流代收</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="意向物流" size="mini" prop="intentional_logistics_id">
          {{(function(){if(params.intentional_logistics_id == 0)params.intentional_logistics_id = null})()}}
          <el-select clearable v-model="params.intentional_logistics_id" filterable @focus="logisticsInvok" @change="logisticsChanges" :filter-method="logisticsInvok">
            <el-option v-for="item in logisticsList" :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="params.abstract"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="!tirebackstorageAddGet.outstock_id">来源单据</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_info" style="width: 100%" @selection-change="handleSelectionChange" max-height="500" :summary-method="getSummaries" show-summary>
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" text-align="center">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="序号" type="index" width="60">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" width="170"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="仓库名称" width="132">
          <template slot-scope="scope">
            <el-select v-model="scope.row.warehouseObj" filterable placeholder="请输入" clearable @focus="getWarehouseInfo(scope.row,scope.$index)" @change="warehouseChange(scope.row,scope.$index)" value-key="allocation_id">
              <ul class="customer customer-ul4 customer-ul2">
                <li>仓库</li>
                <li>货位</li>
                <li style="text-align:right">可用数量</li>
                <li style="text-align:right">在库数量</li>
              </ul>
              <el-option v-for="item in scope.row.wareHouselist" :key="item.allocation_id" :label="item.warehouse_name" :value="item">
                <ul class="customer customer-ul4">
                  <li :title="item.warehouse_name">{{ item.warehouse_name }}</li>
                  <li :title="item.allocation_name">{{item.allocation_name}}</li>
                  <li style="text-align:right" :title="item.available_stocks">{{item.available_stocks}}</li>
                  <li style="text-align:right" :title="item.stocks">{{item.stocks}}</li>
                </ul>
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称" width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="real_num" label="数量" align="right" width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_appraisal_sn" label="来源单号" width="150">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="备注" min-width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" maxlength="300" placeholder="请输入" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submit('TableDataRef')">确 定</el-button>
    </div>
    <chooseProducts v-if="goodsAddressVisible" :message="params.company_id" @getAllocationArr="getAllocationArr" @customerId="getCustomerInfo" />
  </div>
</template>
<script>
import goodsAddress from "@/components/element/inventory/share/goodsAddressOutStorage"; //货位弹框
import getAreaList from "common/components/public/getAreaList";
import chooseProducts from "./chooseProducts.vue";
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  CompanySwitchClear,
  opthionList
} from "common/utils/public_function/commonUtils";
export default {
  name: "instorageOldtyresstorageTirebackstorageAdd",
  data() {
    return {
      authority: {},
      title: "新增",
      goodsAddressVisible: false, //货位列表弹框
      addChandise: false,
      rowIndex: null, //选择的当前表格索引
      multipleSelection: [],
      TableData: {
        goods_info: [] //只有新增的时候需要
      },
      showAllocationList: true,
      params: {
        payment_method: 1
      },
      showDepartment: true,
      defaultArea: [0], //默认的区域id
      loading: false,
      cityList: []
    };
  },
  components: {
    chooseProducts,
    goodsAddress, //货位列表
    getAreaList //区域列表
  },
  computed: {
    ...mapGetters(["tirebackstorageAddGet"])
  },
  methods: {
    ...mapActions(["tirebackstorageIndexAct"]),
    getWarehouseInfo(row, index) {
      //获取仓库 或为 可用数量以及在库 数量
      this.$axMethod
        .getBatchInfo({
          company_id: this.params.company_id,
          goods_id: row.goods_id
        })
        .then(res => {
          if (res.data.code == "200") {
            this.$set(
              this.TableData.goods_info[index],
              "wareHouselist",
              res.data.data
            );
          }
        });
    },
    warehouseChange(row, index) {
      //新增和小强的修改
      if (Array.isArray(row.wareHouselist)) {
        let arr = [
          "batch_id",
          "warehouse_id",
          "warehouse_name",
          "allocation_name",
          "allocation_id"
        ].forEach(item => {
          this.$set(
            this.TableData.goods_info[index],
            item,
            row.warehouseObj[item]
          );
        });
      }
    },
    logisticsChanges(val) {
      // this.$refs.TableDataRef.validateField(["intentional_logistics_id"]);
      this.params.logistics_name = this.logisticsList.filter(
        obj => obj.logistics_id == val
      )[0].logistics_name;
      this.params.intentional_logistics = this.params.logistics_name;
    },
    // paymentMethodChange() {
    //   if (this.params.payment_method === 2) {
    //     setTimeout(() => {
    //       this.$refs.TableDataRef.validateField(["intentional_logistics_id"]);
    //     }, 50);
    //   } else {
    //     this.$refs.TableDataRef.clearValidate("intentional_logistics_id");
    //   }
    // },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    goBack() {
      this.$router.push({
        path: "/inventory/tireback"
      });
      this.tirebackstorageIndexAct({
        goPage: this.tirebackstorageAddGet.goPage,
        params: this.tirebackstorageAddGet.params,
        pageSize: this.tirebackstorageAddGet.pageSize
      });
    },
    addF() {
      if (this.params.company_id) {
        this.goodsAddressVisible = true;
      } else {
        this.$message({
          message: "请先选择库存组织！",
          type: "warning"
        });
      }
    },
    attribute(val) {
      this.addChandise = val;
    },
    getCustomerInfo(val) {
      this.params.customer_id = val;
      let params = {
        customer_id: val,
        company_id: this.params.company_id
      };
      if (!val) return false;
      this.$axMethod
        .getCustomerInfoNEWqx(params)
        .then(({ data }) => {
          // wz 接口返回的时候没有返回对应字段
          if (data.code == 200) {
            let arr = [
              "business_id",
              "department_id",
              "customer_id",
              "customer_name",
              "business_name",
              "hand_over_id",
              "hand_over_name",
              "delivery_demand_id",
              "delivery_demand_name"
            ];
            arr.forEach(item => {
              this.$set(this.params, item, data.data[item]);
            });
            Object.assign(data.data, {
              employee_id: data.data.business_id,
              employee_name: data.data.business_name
            });
            opthionList(this, data.data);
          }
        })
        .then(res => {
          this.$axMethod.getDefaultAddress(params).then(({ data }) => {
            if (data.code == 200) {
              this.$set(this.params, "address", data.data.address);
              this.$set(
                this.params,
                "consignee_name",
                data.data.consignee_name
              );
              this.$set(
                this.params,
                "consignee_mobile",
                data.data.consignee_mobile
              );
              this.defaultAreaName = `${data.data.province_name}/${data.data.city_name}/${data.data.area_name}`;
              this.defaultArea = [
                data.data.province_id,
                data.data.city_id,
                data.data.area_id
              ];
              this.params.cityList = this.defaultArea.join(",");
              this.params.province_id = this.defaultArea[0];
              this.params.city_id = this.defaultArea[1];
              this.params.area_id = this.defaultArea[2];
            }
          });
        });
    },
    getAllocationArr(row) {
      let sparn = row.join(",");
      let vm = this;
      this.$axMethod
        .getreferenceAppraisalInfo({
          company_appraisal_goods_id: sparn
        })
        .then(({ data }) => {
          if (data.code == 200) {
            data.data.forEach(obj => {
              obj.batch = "返胎";
              obj.wareHouselist = [
                {
                  warehouse_id: obj.warehouse_id,
                  warehouse_name: obj.warehouse_name,
                  allocation_id: obj.allocation_id,
                  allocation_name: obj.allocation_name
                }
              ];
              obj.warehouseObj = {
                warehouse_id: obj.warehouse_id,
                warehouse_name: obj.warehouse_name,
                allocation_id: obj.allocation_id,
                allocation_name: obj.allocation_name
              };
            });
            data.data.forEach(item => {
              item.discount_unit_price = 0;
              item.company_id = vm.params.company_id;
              item.company_name = vm.params.company_name;
            });
            this.TableData.goods_info = data.data;
            opthionList(this, this.TableData.goods_info);
          }
        });
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData.goods_info);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.$set(this.TableData, "goods_info", Array.from(differenceABSet));
      } else {
        this.$message({
          message: "请先选择要删除的商品！",
          type: "warning"
        });
      }
    },
    submit(formName) {
      let flag = true;
      if (!this.TableData.goods_info.length) {
        this.$message({
          message: "请先添加商品！",
          type: "warning"
        });
        return false;
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = Object.assign({}, this.TableData, this.params);
          this.TableData.goods_info.forEach(item => {
            item.unit_price = 0;
            delete item.wareHouselist;
            delete item.warehouseObj;
            delete item.subtotal_price;
          });
          params.goods_info = JSON.stringify(this.TableData.goods_info);
          Object.assign(this.params, {
            intentional_logistics: this.params.logistics_name
          });

          let api = !this.params.outstock_id
            ? "getaddOutstock"
            : "geteditOutstock";

          // 校验对接车郎中
          this.$baseData
            .checkCarDoctor({
              company_id: this.params.company_id,
              company_name: this.params.company_name,
              bill_type: 15,
              bill_id: this.params.outstock_id,
              operation_type: this.params.outstock_id ? 2 : 1,
              goods_info: JSON.stringify(this.TableData.goods_info)
            })
            .then(() => {
              this.$axMethod[api](params).then(({ data }) => {
                if (data.code == "200") {
                  this.$message({
                    message: data.msg,
                    type: "success"
                  });
                  this.goBack();
                }
              });
            });
        }
      });
    },
    companyChange(val) {
      CompanySwitchClear(this, this.params);
      this.$set(this.params, "customer_id", null);
      this.$set(this.params, "goods_info", []);
    },
    departmentChange(val) {
      this.$set(this.params, "business_id", "");
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 8) {
            sums[index] = parseFloat(sums[index]);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  mounted() {
    this.title = this.tirebackstorageAddGet.outstock_id ? "修改" : "新增";
    this.params.outstock_id = this.tirebackstorageAddGet.outstock_id;
    if (this.tirebackstorageAddGet.outstock_id) {
      delete this.TableData.goods_info;
      this.$axMethod
        .getcheckOutstock({
          outstock_id: this.tirebackstorageAddGet.outstock_id
        })
        .then(({ data }) => {
          if (data.code == 200) {
            for (const obj of data.data.goods_info) {
              obj.wareHouselist = [
                {
                  warehouse_id: obj.warehouse_id,
                  warehouse_name: obj.warehouse_name,
                  allocation_id: obj.allocation_id,
                  allocation_name: obj.allocation_name
                }
              ];
              obj.warehouseObj = {
                warehouse_id: obj.warehouse_id,
                warehouse_name: obj.warehouse_name,
                allocation_id: obj.allocation_id,
                allocation_name: obj.allocation_name
              };
            }
            this.$set(this.TableData, "goods_info", data.data.goods_info);
            this.$set(this, "params", data.data);
            this.defaultArea = [
              data.data.province_id,
              data.data.city_id,
              data.data.area_id
            ];
            this.params.cityList = this.defaultArea.join(",");
            this.params.province_id = this.defaultArea[0];
            this.params.city_id = this.defaultArea[1];
            this.params.area_id = this.defaultArea[2];
            Object.assign(data.data, {
              employee_id: data.data.business_id,
              employee_name: data.data.business_name
            });
            this.logisticsList = [];
            this.logisticsList.push({
              logistics_id: data.data.intentional_logistics_id,
              logistics_name: data.data.intentional_logistics
            });
            opthionList(this, data.data);
          }
        });
    } else {
      let { employee_id, company_admin_id } = localStorage;
      if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
        this.$set(this, "params", {
          outstock_type_id: 10008,
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          goods_info: [],
          payment_method: 1,
          cityList: ""
        });
        this.$set(this, "TableData", {
          goods_info: []
        });
      } else {
        this.$set(this, "params", {
          outstock_type_id: 10008,
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          department_id: Number(localStorage.department_id),
          business_id: Number(localStorage.employee_id),
          payment_method: 1,
          cityList: ""
        });
        this.$set(this, "TableData", {
          goods_info: []
        });
      }
      opthionList(this, localStorage);
    }
    // this.logisticsInvok();
  }
};
</script>

<style lang="less" scoped>
.dialog-footer {
  margin-top: 40px;
  text-align: center;
}

.dialogTips {
  margin-bottom: 10px;
}

.status-style .cell {
  display: flex;
}
</style>
